body {
    overflow: hidden;
}

a {
    text-decoration: none;
}

.board {
    width: 100vw;
    height: 100vh;
    background-size: 100% 100%;
    background-position: center;
    overflow: hidden;
}

.navbar {
    width: 100vw;
    height: 24px;
    backdrop-filter: blur(10vh);
    display: flex;
}

.navbar-tap {
    color: rgb(243, 243, 243);
    font-weight: 450;
    padding: 0 10px;
    text-shadow: 0 0 4px rgba(255, 255, 255, 0.25);
}

.navbar-over {
    background-color: rgba(255, 255, 255, 0.25);
    border-radius: 3px;
}

.navbar-rigth {
    margin-left: auto;
}

.dock-bar {
    position: absolute;
    bottom: 0;
    width: 600px;
    left: 50%;
    margin-left: -300px;
    backdrop-filter: blur(10vh);
    height: 75px;
    border: rgba(255, 255, 255, 0.1) 1px solid;
    border-radius: 20px;
    display: flex;
    box-sizing: border-box;
    animation-duration: 0.75s;
    animation-fill-mode: both;
    animation-iteration-count: 3;
    animation-play-state: running;
}

.dock-icon-a {
    margin: auto 0 auto 20px;
}

.python {
    background-size: 100% 100%;
    background-position: center;
    background-image: url("../public/img/python.png");
}

.django {
    background-size: 100% 100%;
    background-position: center;
    background-image: url("../public/img/django.png");
}

.js {
    background-size: 100% 100%;
    background-position: center;
    background-image: url("../public/img/javascript.png");
}

.finder {
    width: 1000px;
    height: 500px;
    border: #888888 1px solid;
    border-radius: 10px;
    margin: 100px 400px;
}
.for-drag {
  position: absolute;
  width: 100%;
  height: 50px;
  display:flex;
}

.exit-bar {
    box-sizing: border-box;
    width: 150px;
    height: 100%;
    backdrop-filter: blur(30px);
    float:left;
    background-color: #00000036;
    border-radius: 10px 0px 0px 10px;
    border-right: black 1px solid
}

.drag-bar {
    box-sizing: border-box;
    width: calc(100% - 150px);
    height: 50px;
    background-color: #3e2424;
    float:left;
    border-top-right-radius: 10px;
    border-bottom: black 1px solid
}

.main-div {
    box-sizing: border-box;
    width: calc(100% - 150px);
    height: 450px;
    background-color:  #3e2424;
    float:left;
    border-bottom-right-radius: 10px;
    overflow-y:scroll
}

.file-name {
  margin:1px;
  color: white;
  border-radius: 5px;
  text-align: center;
}

.file {
  height: 110px;
  width: 110px;
  margin: 15px;
  float:left;
}

.file-img {
  width: 85px;
  height: 85px;
  background-size: 100% 100%;
  background-position: center;
  margin:auto;
  border-radius: 5px;
}

.file-img.selected {
    background-color: gray;
}

.file-name.selected {
    background-color:#147bbc;
}

.text-page {
    width: 1200px;
    height: 800px;
    
}

.left-num {
    width: 50px;
    height: 100%;
    background-color: #3d4751;
    float: right;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    overflow: hidden;
}

.right-main {
    width: calc(100% - 50px);
    height: 100%;
    float: right;
    background-color: #343d46;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    overflow: auto;
    color: #ccd2dd;
    font-weight: 600;
    padding: 0px 10px;
    box-sizing: border-box;
    display:grid;
    line-height: 175%;
}

.for-drag-page {
    position: absolute;
    top:0;
    height: 28px;
    width: 100%;
    background-color: #2e2e34;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    display: flex;
}

.numbers {
    box-sizing: border-box;
    border-bottom: #838d98 1px solid;
    text-align: center;
    color:#838d98;
    font-weight: 600;
    padding-top: 5px;
}

pre {
    font: 14px Monaco,"Courier New","DejaVu Sans Mono","Bitstream Vera Sans Mono",monospace;
    color: #343d46;
    border-radius: 3px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #ccc;
    background-color: #ffffff;
    padding: 10px 3px;
}

.exit {
    background-color: #fe5f59;
    margin: auto 0px auto 15px;
}
.disappear{
    background-color: #febb2e;
    margin: auto 0px auto 8px;
}
.full_page {
    background-color: #27c840;
    margin: auto 0px auto 8px;
}

.bar_button {
    height: 10px;
    width: 10px;
    border-radius: 5px;
    
}

.hover_icon {
    color: #2e2e33;
    font-size: 10px;
    text-align: center;
    line-height: 10px;
    font-weight: 600;
    cursor: default;
}

.file-name-on-bar {
    margin: auto;
    color: #a8aeb9;
}

.favorite {
    margin-top: 50px;
    padding-left: 10px;
    color: darkgray;
    font-size: 12px;
}

.categories {
    display:flex;
    color: white;
    margin-top: 10px;
}